import { Box, Flex, Heading, Text } from "@chakra-ui/react";
import React, { useMemo, useState } from "react";
import { Outlet, Link, useLocation, useOutletContext } from "react-router-dom";
import { allProjects, projectMenus } from "../projects/list";
import { LinariaClassName, css, cx } from "@linaria/core";
import { ProjectMenu, ProjectOverview } from "../utils/types";
import { theme } from "../styles";
import Footer from "./Footer";
import ProjectNav from "./ProjectNav";
import { ColorTheme } from "@tanyoknits/shared";

type ContextType = {
  colorTheme: ColorTheme;
};

const styles: Record<string, LinariaClassName> = {
  "pine-pullover": css`
    background-color: #5c330a;
    background-image: linear-gradient(19deg, #5c330a 0%, #948bac 100%);
    min-height: 100vh;
    color: var(--oc-white);
  `,
  "tiara-vest": css`
    background-color: #907c5c;
    background-image: linear-gradient(24deg, var(--theme-8) 0%, #907c5c 100%);
    min-height: 100vh;
    color: var(--oc-white);
  `,
};

export default function Project() {
  const [projectInfo, setProjectInfo] = useState<ProjectOverview | null>(null);
  const [menu, setMenu] = useState<number | null>(null);

  const location = useLocation();
  const [projectID, pageID] = location?.pathname.split("/").slice(1);

  useMemo(() => {
    const page = pageID as ProjectMenu;
    const project = allProjects[projectID as string];
    setProjectInfo(project);
    setMenu(project.menus.indexOf(page ?? "intro"));
  }, [projectID, pageID]);

  if (projectInfo == null) {
    return null;
  }

  const { name, menus } = projectInfo;
  const colorTheme = projectInfo.theme ?? "gray";

  function GotoMenu({ menu, isLast }: { menu: number; isLast: boolean }) {
    const id = isLast ? menu - 1 : menu + 1;
    return (
      <Box m={4}>
        <Link to={menus[id]}>
          <Text fontSize="xl" display="inline" mr={2}>
            {isLast ? `\u2190` : `\u2192`}
          </Text>
          <Text fontSize="xl" variant="link" display="inline">
            {isLast ? "Back to" : "Next: "} {projectMenus[menus[id]]}
          </Text>
        </Link>
      </Box>
    );
  }

  return (
    <Box className={cx(theme[colorTheme], styles[projectID])}>
      <Box
        style={{
          minHeight: "100vh",
          backgroundImage: `url('${process.env.PUBLIC_URL}/bg.svg`,
        }}
      >
        <Flex align="center" justify="space-between" p={4}>
          <Flex>
            <Link to="/">
              <Text fontSize="2xl" pr={2}>{`\u2190`}</Text>
            </Link>
            <Heading as="h1" size="lg">
              <Link to="intro">{name}</Link>
            </Heading>
          </Flex>
          <ProjectNav menus={menus} selectedMenu={menu} />
        </Flex>
        <Outlet context={{ colorTheme }} />
        {menu != null && (
          <GotoMenu isLast={menu === menus.length - 1} menu={menu} />
        )}
        <Footer />
      </Box>
    </Box>
  );
}

export function useProject() {
  return useOutletContext<ContextType>();
}
