import { getRound } from "@tanyoknits/shared";
import { VestMeasurement } from "./sizes";

export interface VestPattern {
  co: number;
  totalSts: number;
}

export function getVestPattern(
  stsPer4Inch: number,
  rowPer4Inch: number,
  measurement: VestMeasurement
): VestPattern {
  const stsGauge = stsPer4Inch / 4;
  // const rowGauge = rowPer4Inch / 4;

  const co = getRound(measurement.shoulder_width * stsGauge, 1);

  return {
    co,
    totalSts: 100,
  };
}
