import { VStack, Text, Flex } from "@chakra-ui/react";

interface Props {
  list: {
    title: string;
    desc: string;
    video: string | undefined;
  }[];
}
export default function Techniques({ list }: Props) {
  return (
    <VStack align="left">
      <Text mb={8}>
        Following techniques are used in the pattern instruction.
      </Text>
      {list.map((t, i) => (
        <Flex key={i} align="top" columnGap={4} flexWrap="wrap">
          <Text variant="bold">{t.title}</Text>
          <Text maxW="2xl">{t.desc}</Text>
          {t.video != null && (
            <Text variant="notes">
              <a href={t.video} target="_blank" rel="noreferrer">
                Youtube link
              </a>
            </Text>
          )}
        </Flex>
      ))}
    </VStack>
  );
}
