import {
  Size,
  FOSizing,
  FOPart,
  baseBodySizing,
  BodyPart,
  vestCoreParts,
  VestCorePart,
  VestMeasurement,
} from "../../utils/sizes";
import { IntroSizingInfo, PageInfo, PatternInfo } from "../../utils/types";
import { VestPattern, getVestPattern } from "../../utils/vestPatternMaker";
import profile from "../juniper-vest/profile";

export const sizes: Size[] = ["S", "M"];

const pageInfo: PageInfo[] = [
  {
    id: "overview",
    label: "Overview",
    isLinear: true,
  },
  {
    id: "back",
    label: "Back top",
    isLinear: true,
  },
  {
    id: "front",
    label: "Front top",
    isLinear: true,
  },
  {
    id: "Body",
    label: "Body from underarm",
    isLinear: true,
  },
  {
    id: "bands",
    label: "Bands",
  },
];

// For Pattern page wrapper
export const patternInfo: PatternInfo = {
  pageInfo,
  sizes,
};

// Base pattern calculation
const baseMeasurement: FOSizing = {
  S: {
    neck_back_depth: 1,
    neck_depth: 4.25,
    shoulder_drop: 1,
    armhole_depth: 10.75,
    underarm_curve_depth: 2.5,
    bust_circumference: 35,
    neck_width: 8,
    shoulder_width: 3.25,
    total_length: 23,
    arm_inset_width: 1.5,
    hem: 2.5,
    neck_band_height: 1.5,
    arm_band_height: 1.5,
  },
  M: {
    neck_back_depth: 1,
    neck_depth: 4.25,
    shoulder_drop: 1,
    armhole_depth: 11.25,
    underarm_curve_depth: 2.5,
    bust_circumference: 39,
    neck_width: 9,
    shoulder_width: 3.5,
    total_length: 24,
    arm_inset_width: 1.5,
    hem: 2.5,
    neck_band_height: 1.5,
    arm_band_height: 1.5,
  },
};

export const measurements: Partial<Record<FOPart, number>>[] = sizes.map(
  (size) => {
    const ms = baseMeasurement[size] ?? {};
    return Object.keys(ms)
      .filter((key) => vestCoreParts.includes(key as VestCorePart))
      .reduce((cur, key) => {
        return Object.assign(cur, { [key]: ms?.[key as VestCorePart] });
      }, {});
  }
);

export const schemaBySize: VestPattern[] = measurements.map((m, i) => {
  return getVestPattern(
    profile.gauge.sts_count,
    profile.gauge.row_count,
    m as VestMeasurement
  );
});

const introInfo: IntroSizingInfo = {
  totalSts: schemaBySize.map((schema) => schema.totalSts),
  bodyParts: ["bust"].map((d) => {
    const part = d as BodyPart;
    return {
      part,
      sizes: sizes.map((size) => baseBodySizing?.[size as Size]?.[part] ?? 0),
    };
  }),
  foParts: ["bust_circumference", "total_length"].map((d) => {
    const part = d as FOPart;
    return {
      part,
      sizes: sizes.map((size) => baseMeasurement?.[size as Size]?.[part] ?? 0),
    };
  }),
  baseSizing: { size: "S", grams: 260 },
  sizes,
};

export default introInfo;
