import { useState } from "react";
import { ColorTheme, Direction, MotifInPattern } from "../utils/types";
import { css, cx } from "@linaria/core";
import Chart, { FocusType } from "./Chart";
import Written from "./Written";
import {
  Flex,
  Heading,
  Radio,
  RadioGroup,
  Stack,
  Text,
  VStack,
  HStack,
  StackDivider,
  Box,
} from "@chakra-ui/react";

interface Props extends MotifInPattern {
  direction?: Direction;
  className?: string;
  colorScheme?: ColorTheme;
}

const styles = {
  title: css`
    text-decoration: underline;
    text-decoration-color: var(--oc-indigo-2);
  `,
  chartWrapper: css`
    /* Hide overflow scrollbar for Chrome, Safari and Opera */
    ::-webkit-scrollbar {
      display: none;
    }
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
  `,
};

export default function InteractiveMotif({
  title,
  stsCount,
  chart,
  direction,
  glyphSize = "L",
  className,
  colorScheme,
}: Props) {
  const [menu, setMenu] = useState<string>("1");
  const [focus, setFocus] = useState<FocusType>([null, null]);

  const selectedDirection: Direction =
    direction != null ? direction : menu === "1" ? "round" : "flat";

  return (
    <VStack align="left" className={cx(className)} spacing={4}>
      {title && <Heading className={cx(styles.title)}>{title}</Heading>}
      <HStack gap={4} divider={<StackDivider borderColor="gray.300" />}>
        {direction == null && (
          <RadioGroup onChange={setMenu} value={menu.toString()}>
            <Stack direction="row">
              <Radio colorScheme={colorScheme} value="1">
                In the round
              </Radio>
              <Radio colorScheme={colorScheme} value="2">
                Flat
              </Radio>
            </Stack>
          </RadioGroup>
        )}
        <Text>
          Repeat <strong>{stsCount}</strong> stitches x{" "}
          <strong>{chart.length}</strong>
          {selectedDirection === "round" ? " rounds" : " rows"}
        </Text>
      </HStack>
      <Flex flexWrap="wrap" gap={4}>
        <Box overflowX="scroll" pt={1} className={cx(styles.chartWrapper)}>
          <Chart
            chart={chart}
            size={glyphSize}
            direction={selectedDirection}
            focus={focus}
            setFocus={setFocus}
          />
        </Box>
        <Written chart={chart} direction={selectedDirection} focus={focus} />
      </Flex>
    </VStack>
  );
}
