import { getChartFromText } from "../../utils";
import { Motif } from "../../utils/types";

export const getMotif = (hasMB: boolean = true): Motif => {
  const chart: string[] = [`k:3 ${Array(7).fill("p k").join(" ")} k:2 * 2`];
  for (let i = 0; i < 5; i++) {
    chart.push(
      `p:${i} c21lp ${Array(6 - i)
        .fill("k p")
        .join(" ")} k c21rp p:${i}`
    );
    chart.push(
      `p:${i + 1} k:3 ${Array(6 - i)
        .fill("p k")
        .join(" ")} k:2 p:${i + 1}`
    );
  }
  if (hasMB) {
    chart.push(
      `p mb p:3 k k p k p k p k:2 p:3 mb p`,
      `p:5 k:2 p k p k p k:2 p:5`
    );
  } else {
    chart.push(`p:5 k k p k p k p k:2 p:5 * 2`);
  }
  for (let i = 4; i > 0; i--) {
    chart.push(
      `p:${i} c21rp ${Array(6 - i)
        .fill("k p")
        .join(" ")} k c21lp p:${i}`
    );
    chart.push(
      `p:${i} k:4 ${Array(6 - i)
        .fill("p k")
        .join(" ")} k:3 p:${i}`
    );
  }
  chart.push(`c21rp ${Array(6).fill("k p").join(" ")} k c21lp`);
  chart.push(`k:4 ${Array(6).fill("p k").join(" ")} k:3`);

  return {
    title: "Diamond",
    stsCount: 19,
    chart: getChartFromText(chart, true),
    stEquivCount: 16.875,
  };
};
